* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#html {
  word-break: break-all;
  color: #333;
  padding: 0 0 0 20px;
  font-size: 14px;
}

#wrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 50%;
  left: 60%;
  transform: translateY(-50%);
}

#bagua {
  position: relative;
}

.text {
  width: 100%;
  text-align: center;
  color: #fff;
  margin-top: 10px;
}

@media (max-width: 500px) {
  #html {
    height: 60vh;
    padding: 10px;
    overflow: auto;
  }

  #wrapper {
    height: 40vh;
    padding: 10px 0;
    position: static;
    top: 0;
    left: 0;
    transform: none;
  }

  #wrapper > .text {
    margin-top: 0;
  }

  #bagua {
    width: 260px;
    height: 260px;
    position: relative;
    top: 0;
    right: 0;
  }

  #bagua:before, #bagua:after {
    width: 130px;
    height: 130px;
  }
}

